@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: "ui-serif", "Georgia", "Cambria", "Times New Roman", "Times",
    "serif";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Sticky navbar */
.sti {
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
}

.section-title {
  @apply text-4xl font-bold mt-20 mb-8;
}

/* Redirect fade-out/in transition*/
.fade-enter {
  opacity: 0;
}

.fade-enter-active {
  opacity: 1;
  transition: opacity 1500ms;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0;
  transition: opacity 500ms;
}